.back {
    background: none;
    outline: none;
    border: none;
    position: fixed;
    right: 20px;
    top: 20px;
    font-size: 2rem;
}
.plate {
    font-family: 'Cascadia Mono', monospace;
}
.input > input {
    font-size: 3rem;
    font-family: 'Cascadia Mono';
    font-weight: 400;
    width: 7ch;
}
.input button {
    background: none;
    outline: none;
    border: none;
    padding: 0;
    width: 3rem;
    height: 3rem;
}
.input button:disabled > svg {
    fill: #969696;
    stroke: #969696;
}
.input button[disabled="false"] > svg {
    fill: #000000;
    stroke: #000000;
}
.input button[loading="true"] > svg {
    transform-origin: 0.9rem 0.9rem;
   animation: search-animation 3s cubic-bezier(.17,.67,.85,.31) 1s infinite;
}
@keyframes search-animation {
    0% {
        transform: scale(1) rotateZ(0deg);
    }
    50% {
        transform: scale(1) rotateZ(540deg);
    }
    100% {
        transform: scale(1) rotateZ(1080deg);
    }
}

.search {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
}
.input {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}
.keyboard {
  width: calc(100vw - 30px);
  position: fixed;
  bottom: 15px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: white;
}
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.row button {
  transition: all 0.1s ease-in-out;
  font-size: 2rem;
  font-weight: 600;
  flex-grow: 1;
  padding: 0;
  font-family: "Cascadia Mono";
  background: none;
  outline: none;
  border: none;
  border-radius: 50%;
}
.row button:active {
  background-color: #eeeeee;
}

.textarea {
    width: 95%;
    margin-left: 2.5%;
    margin-top: 15px;
    min-height: 75px;
    font-size: 1rem;
}
.upload_wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}
.upload {
    margin: 15px auto;
    padding: 10px 40px;
    border-radius: 1000px;
    background: black;
    border: 1px solid black;
    color: white;
    font-size: 1.5rem;
    font-family: 'Cascadia Mono', monospace;
}
.upload:disabled {
    background-color: #969696;
    border: 1px solid #969696;
}
