.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.banner_text {
  font-size: 1.5rem;
  color: #ccc;
  display: block;
  margin: 0.5rem 0;
  text-align: center;
  font-family: 'Cascadia Mono', monospace;
}

.banner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  cursor: pointer;
}

.input {
  width: 100%;
  height: 100%;
  display: none;
}
